const micro = require('micro')
const test = require('ava')
const request = require('request-promise')
const axios = require("axios")



const url = "https://micro.supermario.io/api/get/characters.lambda.js?name=mario"

test(url, async t => {

    const mario = {
        "_id": "5c054977627966094cd725fe",
        "name": "mario",
        "color": "red",
        "skill": "jumpNrun"
    }



    //const { character } = JSON.parse(await request(url))
    const character = (await axios.get(`${url}/?name=mario`, { name: "peach" })).data
    t.deepEqual(character, mario)

})